/* one piece List rgb(255, 56, 56)*/
#one-piece-list .listOfChapters-css ul {
  list-style-type: none;
  counter-reset: index;
  padding: 0;
  max-width: 300px;
  text-decoration: none;

}

/* List element */
#one-piece-list .listOfChapters-css li {
  counter-increment: index;
  display: flex;
  align-items: center;
  padding: 12px 0;

}

/* Element counter */
#one-piece-list .listOfChapters-css li::before {
  content: counters(index, ".", decimal-leading-zero);
  font-size: 1.5rem;

  font-weight: bold;
  min-width: 68px;
  padding-right: 12px;
  -webkit-font-feature-settings: "tnum";
          font-feature-settings: "tnum";
  font-variant-numeric: tabular-nums;

  background-image: linear-gradient(to bottom, rgb(255, 142, 142), rgb(192, 104, 104));
  background-attachment: fixed;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #fff;
}

#one-piece-list .listOfChapters-css li {
  content: counters(index, ".", decimal-leading-zero);
  font-size: 1.3rem;

  font-weight: bold;
  min-width: 380px;
  min-height: 110px;
  max-height: 200px;
  padding-right: 12px;
  -webkit-font-feature-settings: "tnum";
          font-feature-settings: "tnum";
  font-variant-numeric: tabular-nums;

  background-image: linear-gradient(
    to bottom,
    rgb(255, 66, 66),
    rgb(139, 14, 14)
  );
  background-attachment: fixed;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #fff;
  overflow: visible;
}

/* Element separation */
#one-piece-list .listOfChapters-css li + li {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  text-decoration: none;

  width: 10px;
}

